/** Media Queries **/
.formulario {
  margin-top: 5rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin: 0 0 2rem 0;
}
.formulario label {
  flex: 0 0 10rem;
}
.formulario input {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: #d3d3d3;
}
.formulario input[type=submit] {
  background-color: #559435;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .formulario input[type=submit] {
    width: auto;
  }
}
.formulario input[type=submit]:hover {
  background-color: #3f6e28;
  cursor: pointer;
}

